* {
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
  font-family: Poppins, Helvetica, Arial, Sans-Serif, serif;
  scroll-behavior: smooth;
}

html,
h1,
body,
h2,
h3,
h4,
th,
td,
tr,
h5,
h6,
table,
tr,
td,
th,
button,
p,
a,
li,
img {
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
}

header > div:first-of-type {
  padding:  10px 0;
  margin: 0 auto;
  word-wrap: break-word;

}

span {
  font-weight: 400;
  letter-spacing: 1px;
}

header > div:nth-of-type(2) {
  padding-top: 40px;
}

.main_image {
  float: right;
  width: 50%;
}

h1 {
  font-size: 3em;
  font-weight: 800;
  margin-top: 20px;
  text-align: center;
  position: relative;
  z-index: 100;
}

.reviews {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

blockquote {
  margin-left: 10px;
  margin-right: 10px;
}

.review {
  background-color: #082C2A;
  border-radius: 10px;
  width: calc(50% - 20px);
  padding: 30px;
  margin: 20px 0;
}

main {
  padding: 50px 60px;
  margin: 0 auto;
}

p, ul, ol, li {
  font-size: 16px;
  font-weight: 300;
  margin: clamp(1rem,.5em,2.5rem) auto clamp(1rem,.25em,2rem);
  line-height: 1.6;
  text-transform: none;
  color: black;
}

.center {
  text-align: center;
}

main table {
  display: block;
  margin: 15px auto;
  border-collapse: collapse;
  width: fit-content;
  overflow: auto;
}

main tbody {
  width: 100%;
}

main table td {
  border: 1px solid #F7CC30;
  padding: 10px;
  vertical-align: middle
}

.table_container {
  overflow-x: auto;
}

h2, h3 {
  font-family: Mulish,Helvetica,Arial,Sans-Serif,serif;
  font-weight: 700;
  font-size: 2.2em;
  line-height: 1.143;
  margin: clamp(1rem,.5em,2.5rem) auto clamp(1rem,.25em,2rem);
}


.faq > div {
  min-width: 15em;
  position: relative;
  margin-top: 16px;
  width: calc(100% - 215px);
  margin-left: 99px;
}

.select {
  background: #F7CC30;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 2px #F7CC30 solid;
  border-radius: 0.5em;
  padding: 12px 16px;
  cursor: pointer;
}

.select > span {
  font-size: 18px;
  text-align: start;
  text-transform: none;
  letter-spacing: 1px;
  font-weight: 500;
}

.select-clicked {
  border: 1px #FFD60E solid;
  border-bottom: none;
}

.dropdown__element > div > p {
  margin: 0;
}

.menu {
  list-style: none;
  background: #F7CC30;
  border: 1px #F7CC30 solid;
  box-shadow: 0 0.5em 1em rgba(0, 0, 0, 0.2);
  border-radius: 0.5em;
  color: #9fa5b5;
  top: 3em;
  width: 100%;
  opacity: 0;
  display: none;
  transition: 0.2s;
  z-index: 1;
  margin-top: 16px;
}

.menu li {
  padding: 0px 16px;
  border-radius: 0.5em;
  cursor: pointer;
  font-size: 15px;
  display: flex;
  gap: 10px;
  align-items: center;
}

.menu-open {
  display: block;
  opacity: 0.75;
  padding-left: 0;
  background-color: #F7CC30;
}

.dropdown__element {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.dropdown--last {
  margin-bottom: 93px;
}

footer {
  background-color: #FFC421;
  padding: 20px 35px;
}

footer > p {
  color: #fff;
  text-align: center;
}

.list_titles {
  margin: 0;
  display: flex;
  justify-content: space-between;
  gap: 30px;
  width: 100%;
}

.list_titles > li {
  margin: 0;
  white-space: nowrap;
}

.tables {
  display: flex;
  gap: 30px;
  justify-content: center;
}

.header_container {
  position: relative;
  justify-content: center;
  z-index: 233;
  align-items: center;
  display: flex;
  gap: 30px;
}

.list_titles > li > a {
  color: black;
  font-weight: 600;
  text-decoration: none;
}

header > div:first-of-type > div {
  margin: 0 auto;
  width: 90%;
  justify-content: space-between;
  display: flex;
  align-items: center;
}

header > div:first-of-type {
  background-color: #086460;
}

.buttons {
  display: flex;
  gap: 20px;
}

.buttons > a {
  padding: 10px 15px;
  color: #3C4152;
  background-color: #F7CC30;
  text-decoration: none;
  border-radius: 27px;
  font-weight: bold;
}

.buttons > a:first-of-type {
  background-color: transparent;
  border: 2px solid #F7CC30;
  color: #fff;
}

.faq > div {
  min-width: 15em;
  position: relative;
  margin-top: 16px;
  width: calc(100% - 215px);
  margin-left: 99px;
}

