  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.dropdown--last {
  margin-bottom: 93px;
}

footer {
  background-color: #086460;
  padding: 20px 35px;
}

footer > p {
  color: #fff;
  text-align: center;
}

.image_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

.homepage {
  width: 55%;
}

.destek {
  width: 400px;
}

.license {
  width: 40%;
}

.options {
  width: 350px;
}

.mobile {
  width: 200px;
  border-radius: 27px;
}

.logo {
  width: 140px;
}

.homepage_container {
  justify-content: space-around;
}

::marker {
  color: #F7CC30;
}

.list_titles > li > a {
  color: #fff;
}

.bonus {
  width: 530px;
}

blockquote {
  color: #fff;
}

i, cite {
  color: #F7CC30;
}

.border-image {
  border: 2px solid #F7CC30;
}

.mobile_container {
  justify-content: flex-start;
}

.destek_container {
  max-width: 90%;
}

.giris-image {
  width: 30%;
}

.registration {
  width: 450px;
}

.registration_container {
  justify-content: space-around;
}

.bonus_container > div:last-of-type {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lisans_title {
  text-align: center;
}

@media (max-width: 1200px) {
  main {
    padding: 40px 50px;
  }

  .list_titles {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}

@media (max-width: 1024px) {
  .mobile_container, .kayit_container, .destek_container, .lisans_container {
    flex-direction: column;
  }

  .destek_container > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .bonus_container {
    flex-direction: column;
  }

  .bonus {
    width: 100%;
  }

  .destek_container {
    max-width: 100%;
  }

  .mobile {
    width: 250px;
  }

  .license {
    width: 100%;
  }

  .registration_container {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .homepage_container {
    flex-direction: column;
  }

  .options {
    width: 250px;
  }

  .homepage {
    width: 100%;
  }
}

@media (max-width: 720px) {
  .center_image, .destek {
    width: 100%;
  }

  .review {
    width: 100%;
  }

  ul, ol {
    padding-left: 10px;
  }

  main {
    padding: 30px 20px;
  }

  .list_titles {
    flex-direction: column;
    gap: 10px;
  }

  .registration {
    width: 100%;
  }

  .image_container {
    flex-direction: column;
  }

  header > div:first-of-type > div {
    flex-direction: column;
    gap: 20px;
  }

  h1 {
    font-size: 2.1em;
  }

  .options_container {
    flex-direction: column-reverse;
  }
}

.casinomaxi-wrapper {
  background-color: #082C2A;
  border-radius: 10px;
  padding: 30px;
  margin: 20px 0;
}

.casinomaxi-faq__header {
  padding: 30px;
  background-color: #082C2A;
  border-radius: 10px;
  margin-bottom: 10px
}

.casinomaxi-faq__title {
  margin: 0 0 10px;
  display: inline-block;
  font-weight: 900;
  font-size: 32px;
  font-style: italic;
  color: #F7CC30;
  line-height: 38px
}

.casinomaxi-faq__subtitle {
  max-width: 65%;
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 140%;
}

.casinomaxi-faq__body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column
}

.casinomaxi-faq__body-item {
  border-radius: 10px;
  background-color: #f1f5ff
}

.casinomaxi-faq__body-item:not(:last-child) {
  margin-bottom: 10px
}

.casinomaxi-faq__body-details {
  border-radius: 10px;
  outline: 0
}

.casinomaxi-faq__body-details[open] {
  background-color: #082C2A;
}

.casinomaxi-faq__body-details[open] .casinomaxi-faq__body-question {
  color: #fff;
}

.casinomaxi-faq__body-details[open] .casinomaxi-faq__body-question svg {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.casinomaxi-faq__body-question svg {
  fill: #F7CC30;
}

.casinomaxi-faq__body-details[open] .casinomaxi-faq__body-answer p {
  color: #082C2A
}

.casinomaxi-faq__body-question {
  padding: 28px 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  background-color: #082C2A;
  font-family: inherit;
  color: #fff;
  cursor: pointer;
  outline: 0
}

.casinomaxi-faq__body-question svg {
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
  margin-left: 30px;
  z-index: 10;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  height: 30px;
  width: 30px
}

.casinomaxi-faq__body-question::-webkit-details-marker {
  display: none
}

.casinomaxi-faq__body-answer {
  padding: 0 30px 10px
}

.casinomaxi-faq__body-answer p {
  margin: 0;
  font-family: inherit;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 140%
}
